import Vue from 'vue'
import VueRouter from 'vue-router'

import Home from '../views/Home.vue'
import GG from '../views/Ganbatte.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/ganbatte-greg',
    name: 'About',
    component: GG
  }
]

const router = new VueRouter({
  routes
})

export default router
