<template>
  <div class="counter-page">
    <h1>SURPISE COUNTDOWN</h1>
    <span>{{remaining}}</span>
    <h2>What's this? Who knows, you'll have to comeback to find out.</h2>
    <h3>Note: Subject to procrastination, may end up getting delayed to 2023 if lazy.</h3>
  </div>
</template>

<script>
export default {
  data(){
    return{
      DATE: "Dec 24, 2022 00:00:00",
      remaining: "",
      speed: 1000
    }
  },
  methods:{
    countdown:function(){
      this.interval = setInterval(() => {
        var now = new Date().getTime();
        var distance = new Date(this.DATE).getTime() - now;

        var days = Math.floor(distance / (1000 * 60 * 60 * 24));
        var hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
        var minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
        var seconds = Math.floor((distance % (1000 * 60)) / 1000);

        this.remaining = (days + "d " + hours + "h "
        + minutes + "m " + seconds + "s ");
      }, this.speed);
    }
  },
  created:function(){
    this.countdown();
  }
}
</script>
